
  const filterByVaccine = (centers_list, vaccine) => {
    if(vaccine === 'All'){
      return centers_list;
    }
    let filteredCenters = centers_list.map(center => {
      let filteredSesssions = center.sessions.filter(c => {
        return (c.vaccine === vaccine && c.available_capacity > 0)
      });
      if(filteredSesssions.length > 0){
        return center;
      } else{
        return undefined;
      }
    });    
    return filteredCenters.filter(c => c !== undefined);
  };

  export default filterByVaccine;