
  const filterByDose = (centers_list, dose) => {
    if(dose === 'Both'){
      return centers_list;
    }
    let filteredCenters = centers_list.map(center => {
      let filteredSesssions = center.sessions.filter(c => {
        if(c.available_capacity > 0){
            if(dose === "1"){
                return (c.available_capacity_dose1 > 0)
            }else{            
                return (c.available_capacity_dose2 > 0)
            }
        }else{
            return false;
        }
      });
      if(filteredSesssions.length > 0){
        center.sessions = filteredSesssions;
        return center;
      } else{
        return undefined;
      }
    });    
    return filteredCenters.filter(c => c !== undefined);
  };

  export default filterByDose;