import React, { useState } from "react";
import { useTable, useFilters, useSortBy } from "react-table";
import Table from 'react-bootstrap/Table'

export default function BigTable({ columns, data }) {
  const [filterInput, setFilterInput] = useState("");
  const [blockInput, setBlockInput] = useState("");
  const [pincodeInput, setPincodeInput] = useState("");
  const [addressInput, setAddressInput] = useState("");

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
    setFilter
  } = useTable(
    {
      columns,
      data,
      initialState: {
        sortBy: [
            {
                id: 'pincode',
                desc: false
            }
        ]
    }
    },
    useFilters,
    useSortBy
  );

  const handleFilterChange = e => {
    const value = e.target.value || "";
    setFilter("name", value);
    setFilterInput(value);
  };

  const handleBlockFilterChange = e => {
    const value = e.target.value || "";
    setFilter("block_name", value);
    setBlockInput(value);
  };

  const handleAddressFilterChange = e => {
    const value = e.target.value || "";
    setFilter("address", value);
    setAddressInput(value);
  };

  const handlePincodeFilterChange = e => {
    const value = e.target.value || "";
    setFilter("pincode", value);
    setPincodeInput(value);
  };

  // Render the UI for your table
  return (
    <>
      <div className="input-group row"> 
        <div className="form-group col-sm">
          <input
            className="form-control"
            value={filterInput}
            onChange={handleFilterChange}
            placeholder={"Search Hospital Name"}
          />         
        </div> 
        <div className="form-group col-sm">
        <input
          className="form-control"
          value={addressInput}
          onChange={handleAddressFilterChange}
          placeholder={"Search Address"}
        />         
      </div> 
        <div className="form-group col-sm">
          <input
            className="form-control"
            value={blockInput}
            onChange={handleBlockFilterChange}
            placeholder={"Search Block Name"}
          />
        </div>  
        <div className="form-group col-sm">
          <input
            className="form-control"
            value={pincodeInput}
            onChange={handlePincodeFilterChange}
            placeholder={"Search Pincode"}
          />
        </div>  
      </div>   
      <Table className="styled-table" striped bordered hover responsive {...getTableProps()}>
        <thead>
          {headerGroups.map(headerGroup => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map(column => (
                <th
                  {...column.getHeaderProps(column.getSortByToggleProps())}
                  className={
                    column.isSorted
                      ? column.isSortedDesc
                        ? "sort-desc"
                        : "sort-asc"
                      : ""
                  }
                >
                  {column.render("Header")}
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {rows.map((row, i) => {
            prepareRow(row);
            return (
              <tr {...row.getRowProps()}>
                {row.cells.map(cell => {
                  return (
                    <td {...cell.getCellProps()}>{cell.render("Cell")}</td>
                  );
                })}
              </tr>
            );
          })}
        </tbody>
      </Table>
    </>
  );
}