import React, { useMemo, useState, useEffect } from "react";
import axios from 'axios';
import moment from 'moment';
import BigTable from "./Misc/BigTable";
import {data as mockData, states as mockStates, districts as mockDistricts} from './Test';
import "./App.css";
import Column from './Columns/Column';
import CapacityColumn from './Columns/CapacityColumn';
import useWindowDimensions from './Misc/GetDimensions';

import filterByAge from './Filters/filterByAge';
import filterByVaccine from './Filters/filterByVaccine';
import filterByFeeType from './Filters/filterByFeeType';
import filterByDose from './Filters/filterByDose';
import availableCenters from './Filters/availableCenters';

import {vaccinesList, fees, ages, doses} from './Constants/constData';
import { trackPromise } from 'react-promise-tracker';

function App() {
  const { height, width } = useWindowDimensions();
  const MAX_WIDTH = 760;
  const [isMobile, setIsMobile] = useState(false);
  const [centers, setCenters] = useState([]);
  const [displayData, setDisplayData] = useState([]);
  const [states, setStates] = useState([]);
  const [districts, setDistricts] = useState([]);
  const DURATION = 6;
  
  const [vaccines, setVaccines] = useState(vaccinesList);
  const [today, setToday] = useState("");
  const [selectedState, setSelectedState] = useState("");
  const [selectedDistrict, setSelectedDistrict] = useState("");
  const [district, setDistrict] = useState("");
  const [vaccine, setVaccine] = useState("All");
  const [fee, setFee] = useState("All");
  const [age, setAge] = useState("All");
  const [dose, setDose] = useState("Both");
  const [showMsg, setShowMsg] = useState(false);
  const errorMessage = "No Vaccination center is available for booking.";
  const mainTable = React.createRef();

  const mapsUrl = "http://maps.google.com/?q=";
  const calendarByDistrictUrl = "https://cdn-api.co-vin.in/api/v2/appointment/sessions/public/calendarByDistrict?district_id=";
  const statesUrl = "https://cdn-api.co-vin.in/api/v2/admin/location/states";
  const districtsUrl = "https://cdn-api.co-vin.in/api/v2/admin/location/districts/";
  const dateStr = "&date=";
 
  const resetData = () => {
    setDistricts([]);
    resetOnDistrictChange();
  };
  const resetOnDistrictChange = () => {
    setShowMsg(false);
    setDisplayData([]);
    setToday(moment().format('DD-MM-YYYY'));   
  };

  //On page load
  useEffect(() => {   
    setToday(moment().format('DD-MM-YYYY'));
    trackPromise((async () => {      
      const result = await axios(statesUrl);
      //const result = await mockStates; 
      setStates(result.data.states);
    })());
  }, []);

  const columns = useMemo(
    () => [
      {
        Header: "Hospital",
        columns: [
          {
            Header: "Name",
            accessor: "name",
            Cell: e =><a href={mapsUrl + e.value + ", "+ selectedDistrict + ", " + selectedState} target="_blank" rel="noopener noreferrer"> {e.value} </a>
          },
          {
            Header: "Address",
            accessor: "address",
            Cell: e =><a href={mapsUrl + e.value + ", "+ selectedDistrict + ", " + selectedState} target="_blank" rel="noopener noreferrer"> {e.value} </a>
          },
          {
            Header: "Block",
            accessor: "block_name"
          },
          {
            Header: "Pincode",
            accessor: "pincode",            
            Cell: e =><a href={mapsUrl + e.value + ", "+ selectedDistrict + ", " + selectedState} target="_blank" rel="noopener noreferrer"> {e.value} </a>
          }
        ]
      },
      {
        // Second group - Details
        Header: "Details",
        // Second group columns
        columns: [
          {
            Header: "Date",
            accessor: data => {
              let output = [];
              data.sessions.map(item => {
                if(item.available_capacity > 0)
                  output.push(item);
                return null;
              });
              return output;
            },
            Cell: ({ cell: { value } }) => <Column values={value} col='date'/>
          },
          {
            Header: "Available Doses",
            accessor: data => {
              let output = [];
              data.sessions.map(item => {
                if(item.available_capacity > 0)
                  output.push(item);
                return null;
              });
              return output;
            },
            Cell: ({ cell: { value } }) => <CapacityColumn values={value} col='available_capacity'/>
          }
        ]
      }
    ],
    [selectedDistrict, selectedState]
  );

  const filterData = (data, value) => {
    let res = data;
    switch(value) {
      case "age": {
        if(age !== "All")
          res = filterByAge(data, age);
        break;
      }
      case "fee": {
        if(fee !== "All")
          res = filterByFeeType(data, fee);
        break;
      }
      case "vaccine": {
        if(vaccine !== "All")
          res = filterByVaccine(data, vaccine);
        break;
      }
      case "dose": {
        if(dose !== "Both")
          res = filterByDose(data, dose);
        break;
      }
      default: {
        console.log("Invalid choice");
        break;
      }
    }
    return res;  
  };

  const handleFilterData = (data, value) => {
    switch(value){
      case "vaccine":
        data = filterData(data, "age");
        data = filterData(data, "fee");
        data = filterData(data, "dose");
        break;  
      case "fee":
        data = filterData(data, "age");
        data = filterData(data, "vaccine");        
        data = filterData(data, "dose");
        break;
      case "age":
        data = filterData(data, "fee");
        data = filterData(data, "vaccine");        
        data = filterData(data, "dose");
        break;
      case "dose":
        data = filterData(data, "fee");
        data = filterData(data, "vaccine");        
        data = filterData(data, "age");
        break;
      default:
        console.log("Invalid Option");
        break;
    }
    return data;
  };
  
  const handleVaccineChange = (e) => {
    //Scroll
    if(isMobile){
      mainTable.current.scrollIntoView({ behavior: 'smooth' });
    }    
    setVaccine(e);
    let data = centers;
    data = handleFilterData(data, "vaccine");
    setDisplayData(filterByVaccine(data, e));
  };
  const handleFeesChange = (e) => {
    //Scroll
    if(isMobile){
      mainTable.current.scrollIntoView({ behavior: 'smooth' });
    }    
    setFee(e);
    let data = centers;
    data = handleFilterData(data, "fee");
    setDisplayData(filterByFeeType(data, e));
  };
  const handleAgeChange = (e) => {
    //Scroll
    if(isMobile){
      mainTable.current.scrollIntoView({ behavior: 'smooth' });
    }    
    setAge(e);
    let data = centers;
    data = handleFilterData(data, "age");
    setDisplayData(filterByAge(data, e));
  };
  const handleDoseChange = (e) => {
    //Scroll
    if(isMobile){
      mainTable.current.scrollIntoView({ behavior: 'smooth' });
    }    
    setDose(e);
    let data = centers;
    data = handleFilterData(data, "dose");
    setDisplayData(filterByDose(data, e));
  };
  const handleStateChange = (e, stateText) => {
    if(width < MAX_WIDTH){
      setIsMobile(true);
    }
    setSelectedState(stateText);
    //reset data
    setCenters([]);
    resetData();
    trackPromise((async () => {
      const result = await axios(districtsUrl+e); 
      //const result = await mockDistricts;
      setDistricts(result.data.districts);
    })());
  };
  const handleDistrictChange = (event, t) => {  
      //Scroll
      if(isMobile){
        mainTable.current.scrollIntoView({ behavior: 'smooth' });
      }      
      let e = "";
      resetOnDistrictChange();
      let day = today;
      let dist = district;
      if(event){
        setSelectedDistrict(event.target.options[event.target.selectedIndex].text);
        e = event.target.value;
        dist = e;
        setDistrict(dist === "Select" ? "" : dist);
      }
      if(t){
        day = t;        
        setToday(day);   
      }
      trackPromise((async () => {      
      if(dist !== "Select"){
        const result = await axios(calendarByDistrictUrl+dist+dateStr+day);
        //const result = await mockData;
        let avCenters = availableCenters(result.data.centers);
        setCenters(avCenters);
        if(avCenters.length > 0){
          setShowMsg(false);
          avCenters = filterData(avCenters, "vaccine");
          avCenters = filterData(avCenters, "fee");
          avCenters = filterData(avCenters, "age");
          avCenters = filterData(avCenters, "dose");
          setDisplayData(avCenters);
        }else{
          setShowMsg(true);
        }
      }              
    })());
  };
  const fetchCentersData = (e) => {
    let res = '';
    if(e === 'next'){
      res = moment(today,'DD-MM-YYYY').add(DURATION, 'd');
    }else{
      let prevDay = moment(today,'DD-MM-YYYY').subtract(DURATION, 'd');
      let currentDay = moment();
      if(prevDay.isBefore(currentDay)){
        res = currentDay;
      }else{
        res = prevDay;
      }      
    }
    let resDay = res.format('DD-MM-YYYY'); 
    handleDistrictChange("", resDay);
  };
  const fetchUntilDate = () => {
    return moment(today,'DD-MM-YYYY').add(DURATION, 'd').format('DD-MM-YYYY');
  }
  
  return (
    <div className="App">
      <div className="container">
        <div className="jumbotron">
          <h1>COWIN Availability Tracker</h1>
        </div>
        <form className="form-inline">
          <div className="input-group row">
            <div className="form-group col-sm">
              <label>State:</label>
              <select className="form-control" id="state" name="state" onChange={event => handleStateChange(event.target.value, event.target.options[event.target.selectedIndex].text)}>
                <option>Select</option>
                {states.map((e, key) => {
                    return <option key={key} value={e.state_id}>{e.state_name}</option>;
                })}
              </select>
            </div> 
            <div className="form-group col-sm">
              <label>District:</label>
              <select className="form-control" id="dist" onChange={event => handleDistrictChange(event)}>
                <option>Select</option>        
                {districts.map((e, key) => {
                  return <option key={key} value={e.district_id}>{e.district_name}</option>;
                })}
              </select>
            </div>          
            <div className="form-group col-sm">
              <label>Vaccine:</label>
              <select className="form-control" id="vaccine" onChange={event => handleVaccineChange(event.target.value)}> 
                <option>All</option>            
                {vaccines.map((e, key) => {
                  return <option key={key} value={e.vaccine_name}>{e.vaccine_name}</option>;
                })}
              </select>
            </div>
            <div className="form-group col-sm">
              <label>Fees:</label>
              <select className="form-control" id="sel1" onChange={event => handleFeesChange(event.target.value)}> 
                <option>All</option>            
                {fees.map((e, key) => {
                  return <option key={key} value={e.fee_type}>{e.fee_type}</option>;
                })}
              </select>
            </div>
            <div className="form-group col-sm pb-2">
              <label>Age:</label>
              <select className="form-control" id="age" onChange={event => handleAgeChange(event.target.value)}> 
                <option>All</option>            
                {ages.map((e, key) => {
                  return <option key={key} value={e.min_age_limit}>{e.min_age_limit}</option>;
                })}
              </select>
            </div>
            <div className="form-group col-sm pb-2">
              <label>Dose:</label>
              <select className="form-control" id="dose" onChange={event => handleDoseChange(event.target.value)}> 
                <option>Both</option>            
                {doses.map((e, key) => {
                  return <option key={key} value={e.dose}>{e.dose}</option>;
                })}
              </select>
            </div>
            <div className="btn-group-vertical col-sm">
              <button type="button" className="btn btn-primary btn-sm mb-1" disabled={!district} onClick={e => fetchCentersData('next')}>{today} {">>"}</button>
              <button type="button" className="btn btn-primary btn-sm mb-2" disabled={!district} onClick={e => fetchCentersData('prev')}>{fetchUntilDate()} {"<<"}</button>
            </div>
          </div>
        </form>
        <div id="mainTable" ref={mainTable}>
          <BigTable columns={columns} data={displayData} />    
        </div>
        <br/> 
        {showMsg && (
          <p className="error"> {errorMessage} </p>
        )}   
      </div>
    </div>
  );
}

export default App;