const Column = ({ values, col, dose }) => {    
    return (
      <>
        {values.map((value, idx) => {
            return (
              <div key={idx}>
                <span style={{whiteSpace: 'nowrap'}}>{value[col]} {dose}</span>
              </div>
            )
        })}
      </>
    )
  };

export default Column;
  