const filterByAge = (centers_list, age) => {
    if(age === 'All'){
      return centers_list;
    }
    age = parseInt(age);
    let filteredCenters = centers_list.map(center => {
      let filteredSesssions = center.sessions.filter(c => {
        return (c.min_age_limit === age)
      });
      if(filteredSesssions.length > 0){
        return center;
      } else{
        return undefined;
      }
    });    
    return filteredCenters.filter(c => c !== undefined);
  };

  export default filterByAge;