const availableCenters = (centers_list) => {
    let filteredCenters = centers_list.map(center => {
      let filteredSesssions = center.sessions.filter(c => {
        return (c.available_capacity > 0)
      });
      if(filteredSesssions.length > 0){
        return center;
      } else{
        return undefined;
      }
    });    
    return filteredCenters.filter(c => c !== undefined);
  };

  export default availableCenters;