const CapacityColumn = ({ values, col, dose }) => {    
    return (
      <>
        {values.map((value, idx) => {
            return (
              <div key={idx}>
                <span style={{whiteSpace: 'nowrap'}}>
                  {value[col]} ({'D1:'} {value['available_capacity_dose1']}, {'D2:'} {value['available_capacity_dose2']})  {dose}
                </span>
              </div>
            )
        })}
      </>
    )
  };

export default CapacityColumn;
  